export default function Resultado(props){
    const { peso, guardarPeso, altura, guardarAltura, valorIMC, guardarValorIMC , avisoIMC, guardarAvisoIMC } = props;
    /*
    console.log("peso = ", peso);
    console.log("altura = ", altura);
    console.log("valorIMC = ", valorIMC);
    */
    console.log("[Resultado] avisoIMC = ", avisoIMC);
    let aviso;
    if((avisoIMC === undefined) || (avisoIMC === "-") || (avisoIMC === "(Error!!!)"))
        aviso = <div><p>Por favor,</p><p>Ingrese los datos correcto.</p></div>;
    else
        aviso = <div><p>Para un peso de {peso}Kg. y una talla de {altura} metros, su IMC es {valorIMC}.</p><p>Usted se encuentra en el grupo: {avisoIMC}.</p></div>;

    return aviso;
}

