import React,{Fragment, useState} from "react";
import { CalcularImc } from "./CalcularImc";
import { avisoImc } from "./AvisoImc";
// import { Resultado } from "./Resultado";

const Formulario = (props) => {
    const { peso, guardarPeso, altura, guardarAltura, valorIMC, guardarValorIMC , avisoIMC, guardarAvisoIMC } = props;
    const [error, guardarError] = useState(false);  
  
    const procesarIMC = e => {
        console.log(e);
        e.preventDefault();
    /*
        if(cantidad===0 || plazo==='') {
            console.log('Hay un ERROR...')
            guardarError(true);
            return;
        }
        guardarError(false);

        // {(error)? <p className="error">Todos los campos son obligatorios.</p> : null}

        */
        console.log("Peso = ", peso);
        console.log("Altura = ", altura);
        const valorIMC = CalcularImc(peso, altura);
        console.log("IMC=", valorIMC);
        guardarValorIMC(valorIMC);
        let avisoVal = "(Error!!!)";
        if(valorIMC > 0) {
            avisoVal = avisoImc(valorIMC);
        }
        console.log("Aviso=", avisoVal);
        guardarAvisoIMC(avisoVal);
        // Resultado(peso, altura, valorIMC, avisoIMC);
    }

    return (
        <Fragment>
            <form onSubmit={procesarIMC}>
            <div class="form-container">
                <div class="form-item"><label>Peso:</label></div>
                <div class="form-item">
                    <input placeholder="en kilogramos." type="text" name="Peso"
                    onChange={ e => guardarPeso(parseFloat(e.target.value)) }></input>
                </div>
                <div class="form-item">&nbsp;</div>
                <div class="form-item-ejemplo">(Ejemplo: 76)</div>
                <div class="form-item"><label>Altura:</label></div>
                <div class="form-item">
                    <input placeholder="en metros." type="text" name="Altura"
                    onChange={ e => guardarAltura(parseFloat(e.target.value)) }></input>
                </div>
                <div class="form-item">&nbsp;</div>
                <div class="form-item-ejemplo">(Ejemplo: 1.80)</div>
                <div class="form-item">&nbsp;</div>
                <div class="form-item"><button>CALCULAR</button></div>
            </div>
            </form>
        </Fragment>
    );
}

export default Formulario;
