export function avisoImc(valIMC){
    console.log("AvisoIMC(Peso)=", valIMC);
    let aviso;
    if(valIMC <= 18.5)
    aviso = "Peso Insuficiente";
    else if (valIMC <= 24.9)
    aviso = "Peso Normal";
    else if (valIMC <= 26.9)
    aviso = "Sobrepeso Grado I";
    else if (valIMC <= 29.9)
    aviso = "Sobrepeso Grado II (Presobesidad)";
    else if (valIMC <= 34.9)
    aviso = "Obesidad Tipo I";
    else if (valIMC <= 39.9)
    aviso = "Obesidad Tipo II";
    else if (valIMC <= 49.9)
    aviso = "Obesidad Tipo III (M贸rbida)";
    else
    aviso = "Obesidad Tipo VI (Extrema)";
  
    return aviso;
  }
  
  